<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>

        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s6">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="id" disabled="">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="firstname" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="lastname" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="department" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                    </div>
                </div>

                <button type="submit" class="btn">Submit</button>
                <router-link to="/">Cancel</router-link>
            </form>
            
        </div>
    </div>

</template>

<script>

    import db from './firebase_init'

    export default {
        name: 'edit-employee',
        data() {
            return {
                id: null,
                firstname: null,
                lastname: null,
                department: null,
                position: null
            }
        },

        beforeRouteEnter: function(to, from, next) {
            //console.log(to.params.id);

            db.collection('employees').where('id', '==', to.params.id).get().then(querySnapshot => {
                querySnapshot.forEach(val => {
                    next(vm => {                       
                        vm.id = val.data().id,
                        vm.firstname = val.data().firstname,
                        vm.lastname =  val.data().lastname,
                        vm.department = val.data().department,
                        vm.position = val.data().position
                    })                    
                })
            })
        },

        watch: {
            '$route': 'fetchData'
        },

        methods: {
            fetchData() {

                db.collection('employees').where('id', '==', this.$route.params.id).get().then(querySnapshot => {
                    querySnapshot.forEach(val => {
                        this.id = val.data().id,
                        this.firstname = val.data().firstname,
                        this.lastname = val.data().lastname,
                        this.department = val.data().department,
                        this.position = val.data().position
                    })
                })
            },

            updateEmployee() {
                db.collection('employees').where('id', '==', this.$route.params.id).get().then(querySnapshot => {
                    querySnapshot.forEach(val => {
                        val.ref.update({
                            id: this.id,
                            firstname: this.firstname,
                            lastname: this.lastname,
                            department: this.department,
                            position: this.position
                        }).then(() => {
                            this.$router.push({name: 'view-employee', params:{id: this.id} })
                        })
                    })
                })
            }
        }
    }
</script>


