import db from '../../components/firebase_init'
//import ComponentA from './AddEmployee.vue'
//import Clock from 'vue-clock2';


export default {
    name: 'dashboard',
    //components: { Clock },

    data: function() {
        return {
            employees: [],
            fullname: [],
            AddProductData: {
                id: '',
                firstname: '',
                lastname: '',
                department: '',
                position: ''
            },
            isEdit: false,
            currentPage : 3
        }
    },

    meta: {
        requiresAuth: true
    },

    created: function() {
        this.getProducts()
    },

    computed: {
    },

    methods: {
        getProducts() {

            db.collection('employees').get().then(querySnapshot => {
                const employees = []
                querySnapshot.forEach(doc => {                        
                    employees.push(doc.data())

                })
                this.employees = employees
            })
        },

        onAdd() {
            db.collection('employees').add(this.AddProductData).then(this.getProducts)

            this.$router.push("/");
        },

        onDelete($id) {

            if (confirm('Are you sure?')) {
                    db.collection('employees').where('id', '==', $id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete().then(this.getProducts)
                    })
                })
            }
        },

        onEdit($id) {
            this.isEdit = true

            db.collection('employees').where('id', '==', $id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        'firstname': doc.data().firstname,
                        'lastname': doc.data().lastname,
                        'department': doc.data().department,
                        'position': doc.data().position
                    }
                    //this.EditProductData.push(data);
                    this.EditProductData.push(data)
                    console.log(data)
                })
            })
        },

        onCancel($id) {
            this.isEdit = false
            this.EditProductData.firstname = ''
            this.EditProductData.lastname = ''
        }
    }
}


