<template>
    <div id="view-employee">
        <h3>View Employee</h3>

        <ul class="collection with-header">
            <li class="collection-item"><strong>ID Number:</strong> {{ id }}</li>
            <li class="collection-item"><strong>Fullname: </strong>{{ fullname }}</li>
            <li class="collection-item"><strong>Department: </strong>{{ department }}</li>
            <li class="collection-item"><strong>Position: </strong>{{ position }}</li>
        </ul>

        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteEmployee" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-employee', params: {id: id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>

</template>

<script>
    import db from './firebase_init'

    export default {
        name: 'view-employee',
        data: function() {
            return {
                id: null,
                firstname: null,
                lastname: null,
                fullname: null,
                department: null,
                position: null
            }
        },

        beforeRouteEnter: function(to, from, next) {
            //console.log(to.params.id);

            db.collection('employees').where('id', '==', to.params.id).get().then(querySnapshot => {
                querySnapshot.forEach(val => {
                    next(vm => {                       
                        vm.id = val.data().id,
                        vm.firstname = val.data().firstname,
                        vm.lastname =  val.data().lastname,
                        vm.fullname = val.data().firstname + " " + val.data().lastname,
                        vm.department = val.data().department,
                        vm.position = val.data().position
                    })                    
                })
            })
        },

        watch: {
            '$route': 'fetchData'
        },

        methods: {
            fetchData() {

                //console.log(this.$route.params);

                db.collection('employees').where('id', '==', this.$route.params.id).get().then(querySnapshot => {
                    querySnapshot.forEach(val => {
                        this.id = val.data().id,
                        this.firstname = val.data().firstname,
                        this.lastname = val.data().lastname,
                        this.fullname = val.data().firstname + " " + val.data().lastname,
                        this.department = val.data().department,
                        this.position = val.data().position
                    })
                })
            },

            deleteEmployee() {
                if (confirm('Are you sure?')) {
                        db.collection('employees').where('id', '==', this.$route.params.id).get().then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        })
                    })
                }
            }
        }
    }
</script>


