<template>
    
    <div class="signup">
        <h3>Sign Up</h3>
        <input type="email" v-model="email" placeholder="Email">
        <input type="text" v-model="username" placeholder="username">
        <input type="password" v-model="password" placeholder="Password">
        
        <button @click="signUp" class="btn btn-success">Signup</button>
        <p>You don't have an account? You can create one</p>
    </div>

</template>

<script>
    import db from './firebase_init'
    import firebase from 'firebase'

    export default {
        name: 'signup',

        data: function() {
            return {
                email: '',
                username: '',
                password: ''
            }
        },

        computed: {
            validation: function () {
                return {
                    name: !!this.newUser.name.trim(),
                    email: emailRE.test(this.email)
                }
            },
            
            isValid: function () {
                var validation = this.validation
                return Object.keys(validation).every(function (key) {
                    return validation[key]
                })
            }
          },

        methods: {
            signUp() {
                firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(function(user) {
                    alert('Your account has been created!');

                    //window.location = "/login"
                    this.redirect();
                }),
                function (error) {
                    alert('Oops.' + error.message)
                }

                
            },

            redirect() {
                this.$router.push('/')
            }

        }

        
    }
</script>

<style type="text/css">
    .signup {
        margin-top: 69px;
        margin: auto;
        width: 40%;
        border: 1px solid #ddd;
        padding: 20px;
    }

    .signup input {
        margin: 10px 0;
        width: 20%;
        padding: 15px;
    }

    .signup button {
        cursor: pointer;
    }

    .signup p {
        margin-top: 40px;
        font-size: 13px;
    }

</style>


